//   

.arrows {
  //       
  display: flex;
  gap: 32px;

  &--rigth {
    //        
    justify-content: flex-end;
  }
}

.arrow-left,
.arrow-right {
  //     
  fill: $color-black;

  background-color: transparent;
  border: none;
  cursor: pointer;

  transition: fill $transition-time $cubic-bezier;

  &--white {
    //     
    fill: $color-white;
  }

  &:hover,
  &:focus {
    fill: $color-brand;
  }
}